
/**
 * @fileoverview A service for managing the public gallery of portraits using Firebase.
 */
'use server';

import { initializeApp, getApps } from 'firebase/app';
import { 
    getFirestore, 
    collection, 
    getDocs, 
    addDoc, 
    doc, 
    updateDoc, 
    increment,
    orderBy,
    query,
    Timestamp,
    serverTimestamp,
    deleteDoc
} from 'firebase/firestore';
import { getStorage, ref as clientRef, deleteObject } from "firebase/storage";
import { firebaseConfig } from '@/lib/firebase';
import { adminDb, adminStorage } from '@/lib/firebase-admin';
import { getDownloadURL } from 'firebase-admin/storage';


// Initialize Client Firebase
const app = getApps().length ? getApps()[0] : initializeApp(firebaseConfig);
const db = getFirestore(app);
const storage = getStorage(app);

const portraitsCollectionRef = collection(db, 'portraits');

export interface Portrait {
    id: string;
    petName: string;
    hatStyle: string;
    imageUrl: string;
    votes: number;
    createdAt: Timestamp;
}

/**
 * Retrieves all portraits from the gallery, sorted by creation date.
 * @returns A promise that resolves to an array of portraits.
 */
export async function getGalleryPortraits(): Promise<Portrait[]> {
    const q = query(portraitsCollectionRef, orderBy('createdAt', 'desc'));
    const snapshot = await getDocs(q);
    if (snapshot.empty) {
        return [];
    }
    return snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() } as Portrait));
}

/**
 * Adds a new portrait to the gallery. This involves uploading the image to
 * Firebase Storage using the Admin SDK and then creating a new document in Firestore.
 * @param data - The data for the new portrait.
 * @returns A promise that resolves when the portrait has been added.
 */
export async function addPortraitToGallery(data: { petName: string; hatStyle: string; portraitDataUri: string; }): Promise<void> {
    
    const { portraitDataUri, petName, hatStyle } = data;
    
    // 1. Upload image to Firebase Storage using the Admin SDK
    const bucket = adminStorage.bucket();
    const fileName = `portraits/${Date.now()}_${petName.replace(/\s+/g, '_')}.png`;
    const file = bucket.file(fileName);
    
    // The data URI needs to be stripped of its prefix before uploading
    const uploadData = portraitDataUri.split(',')[1];
    const imageBuffer = Buffer.from(uploadData, 'base64');
    
    await file.save(imageBuffer, {
        metadata: {
            contentType: 'image/png',
        },
    });

    // 2. Get the public URL of the uploaded image
    const imageUrl = await getDownloadURL(file);

    // 3. Create a new document in Firestore using the Admin SDK for consistency
    const portraitsAdminCollectionRef = collection(adminDb, 'portraits');
    await addDoc(portraitsAdminCollectionRef, {
        petName,
        hatStyle,
        imageUrl,
        votes: 0,
        createdAt: serverTimestamp(),
    });
}


/**
 * Increments the vote count for a specific portrait.
 * @param portraitId The ID of the portrait to vote for.
 * @returns A promise that resolves when the vote has been counted.
 */
export async function updateVoteCount(portraitId: string): Promise<void> {
    const portraitRef = doc(db, 'portraits', portraitId);
    await updateDoc(portraitRef, {
        votes: increment(1)
    });
}

/**
 * Deletes a portrait from Firestore and the corresponding image from Storage.
 * @param portraitId The ID of the Firestore document.
 * @param imageUrl The URL of the image in Firebase Storage.
 */
export async function deletePortraitFromGallery(portraitId: string, imageUrl: string): Promise<void> {
    // 1. Delete the document from Firestore
    const portraitRef = doc(db, 'portraits', portraitId);
    await deleteDoc(portraitRef);

    // 2. Delete the image from Firebase Storage using the client SDK ref
    try {
        const storageRef = clientRef(storage, imageUrl);
        await deleteObject(storageRef);
    } catch (error: any) {
        // It's possible the image doesn't exist, so we log the error but don't rethrow it.
        if (error.code === 'storage/object-not-found') {
            console.warn(`Image not found in Storage, but continuing with Firestore deletion: ${imageUrl}`);
        } else {
            throw error;
        }
    }
}
